import React from "react";

import { NavLink } from "react-router-dom";

import {
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardTitle,
  Row,
  ButtonGroup,
  Col
} from "reactstrap";

import Dropdown from "../../../components/Dropdown/Dropdown.jsx";
import StudentsRegistered from "../DescripitiveData/StudentsRegistered";
import SubjectDomain from "../DescripitiveData/SubjectDomain";
import MinimumPercentage from "../DescripitiveData/MinimumPercentage";
import HelpIcon from "../../../components/HelpIcon/HelpIcon.jsx";

import { LevelChart, LevelChartChild } from "../Chart/LevelChart/LevelChart.jsx";
import { ResourcesChart, ResourcesChartChild } from "../Chart/ResourcesChart/ResourcesChart.jsx";
import { ReachStatisticsChart, ReachStatisticsChild } from "../Chart/ReachStatisticsChart/ReachStatisticsChart.jsx";
import { PerformanceChart } from "../Chart/PerformanceChart/PerformanceChart.jsx";
import { MissionChart, MissionChartChild } from "../Chart/MissionChart/MissionChart.jsx";

class ClassInfo extends React.Component{ 
render() {
  const { courseId, classroomId, curriculumId, optionSelected } = this.props;
  return(
    <div>
      <Row>
        <Col lg="4" md="6" sm="9">
          <StudentsRegistered courseId={ courseId } classroomId={ classroomId }/>
        </Col>
        <Col lg="4" md="6" sm="9">
          <SubjectDomain courseId={ courseId } classroomId={ classroomId } curriculumId={ curriculumId }/>
        </Col>
        <Col lg="4" md="6" sm="9">
          <MinimumPercentage classroomId={ classroomId } curriculumId={ curriculumId }/>
        </Col>
        <Col md="12">
          <Card tag = "h5">
            <CardHeader>
              <CardTitle>
                <div className = "d-flex justify-content-end mx-3"> 
                  <div className = "mr-auto p-2">
                    <div className="text-center">Progresso de Interao da Turma com os Recursos</div>
                  </div>
                  <div className = "p-2">
                      <ButtonGroup>
                          <Col>
                            <HelpIcon id = "1" icon = "fa fa-info-circle" 
                            help = {
                              <div className="text-center">
                                Este grfico mostra o progresso 
                                da turma com o passar do tempo 
                                e o impacto das misses criadas na 
                                porcentagem da interao da 
                                turma com os recursos.
                              </div>
                            }/>
                          </Col>
                          <Col>
                            { optionSelected === "Dinmicas" ? (
                                <HelpIcon id = "2" icon = "fa fa-exclamation-triangle" color="red"
                                help = {
                                  <div className="text-center">
                                    Est faltando 2 dias para para o perodo previsto para o domnio deste assunto finalizar e ainda 50% dos alunos no alcanaram a porcentagem mnima de interao com os recursos. Voc pode criar uma misso para motivar esses alunos.
                                  </div>
                                }/>
                              ) : (
                                <HelpIcon id = "2" icon = "fa fa-exclamation-triangle"
                                help = {
                                  <div className="text-center">
                                    Nenhum alerta para este assunto.
                                  </div>
                                }/>
                              )
                            }
                          </Col>
                      </ButtonGroup>
                  </div>  
                </div>
              </CardTitle>
            </CardHeader>
            <CardBody>
              <div style = {{ borderTop: "0.1em solid grey", opacity: "0.3", margin: "0 0.5em 1em 0.5em" }}></div>
                <div>
                  <PerformanceChart classroomId={ classroomId } curriculumId={ curriculumId } courseId={ courseId }/>
                </div>
            </CardBody>
            <CardFooter>
            </CardFooter>
          </Card>
        </Col>
        <Col md="12">
          <Dropdown 
            id = "3" icon = "fa fa-info-circle" title = "Alcance da Porcentagem Mnima"
            help = {
              <div className="text-center">
                Este grfico mostra a quantidade de alunos 
                que atingiram e no atingiram a porcentagem 
                mnima de interao com os recursos deste assunto.
              </div>
            }
            content = {
              <ReachStatisticsChart classroomId={ classroomId } curriculumId={ curriculumId } courseId={ courseId }/>
            }
            contentchild = {
              <ReachStatisticsChild classroomId={ classroomId } curriculumId={ curriculumId } courseId={ courseId }/>
            }
          />
        </Col>
        <Col md="12">
          <Dropdown 
            id = "4" icon = "fa fa-info-circle" title = "Interao da Turma com os Recursos"
            help = {
              <div className="text-center">
                Este grfico mostra a quantidade de alunos que 
                interagiram com sucesso, que no interagiram com 
                sucesso e alunos que no interagiram com cada recurso 
                adicionado pelo professor no plano de atividades deste assunto.
              </div>
            }
            content = {
              <ResourcesChart classroomId={ classroomId } curriculumId={ curriculumId } courseId={ courseId }/>
            }
            contentchild = {
              <ResourcesChartChild classroomId={ classroomId } curriculumId={ curriculumId } courseId={ courseId }/>
            }
          />
        </Col>
        <Col md="12">
          <Dropdown 
            id = "5" icon = "fa fa-info-circle" title = "Distribuio da Turma por Nvel"
            help = {
              <div className="text-center">
                Este grfico mostra a distribuio dos 
                alunos em diferente nveis. Os nveis utilizam 
                a pontuao conquistada pelos estudantes atravs 
                das suas interaes com os recursos do sistema.
              </div>
            }
            content = {
              <LevelChart courseId={ courseId } classroomId={ classroomId }/>
            }
            contentchild = {
              <LevelChartChild courseId={ courseId } classroomId={ classroomId }/>
            }
          />
        </Col>
        <Col md="12">
          <Dropdown 
            id = "6" icon = "fa fa-info-circle" title = "Interao com as Misses"
            help = {
              <div className="text-center">
                Este grfico mostra a quantidade de 
                alunos que alcanaram, no alcanaram 
                e no tentaram alcanar cada misso criada pelo professor.
              </div>
            }
            content = {
              <MissionChart classroomId={ classroomId } curriculumId={ curriculumId } courseId={ courseId }/>
            }
            contentchild = {
              <MissionChartChild classroomId={ classroomId } curriculumId={ curriculumId } courseId={ courseId }/>
            }
          />
        </Col>
      </Row>
      <center>
            <NavLink className="text-dark" to={{pathname:"/dashboardQuestions", state: {DashboardID: 3}}} > 
            <button type="button" style={{marginBottom: '0'}} className="btn btn-primary">
              Avaliar o Painel
            </button>
            </NavLink>
      </center>  
    </div>
    );
  }
}

export default ClassInfo;
