import React from "react";

import {
  Input,
  Button,
  Col
} from "reactstrap";

import { Link } from 'react-router-dom';

import Header from "./components/Header";

class StarterTerm extends React.Component {
    constructor(props)
    {
        super(props);
        this.state = {
            terms : '0',
            checkValid: false,
        }
        this.handleCheckbox = this.handleCheckbox.bind(this);
    }

    componentDidMount() {
        sessionStorage.clear();
    }
    
    handleCheckbox() {
        this.setState(prevState => ({
            checkValid : !prevState.checkValid
        }));
    }
    accept()
    {
        sessionStorage.setItem('accepted', true);
    }
    render () {
        return(
            <div>
            <Header/>
            <Col sm="50" md={{ size: 8, offset: 2 }} className="text-justify">
            <p className= " text-center font-weight-bold " style={{fontFamily: 'Calibri', fontSize: '25px'}}>Termo de Consentimento Livre e Esclarecido</p>
            <br/>
            <p className= " text-justify font-weight-bold " style={{fontFamily: 'Calibri', fontSize: '20px'}}>Leia atentamente o formulrio de consentimento abaixo e comece a participar do estudo se voc concorda com os termos de consentimento.</p>
            <br/>
            <p className= " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}> 
            Voc est sendo convidado a participar de uma pesquisa de opinio. O objetivo desta pesquisa  avaliar quais informaes dos estudantes, apresentadas atravs de diferentes painis de dados, melhor auxilia o professor no processo de tomada de deciso durante o ensino/aprendizagem em uma plataforma de ensino gamificada. 
            </p>
            <br/>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
              <strong> Ttulo do Estudo: </strong> Avaliao de diferentes painis de dados para o auxlio de professores no processo de tomada de deciso em Ambientes Educacionais Gamificados.
            </p>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
              <strong> Pesquisadores: </strong> 
              <ul>
                <li className = " text-justify "> Kamilla Tenrio (kktas@ic.ufal.br)</li>
                <li className = " text-justify "> Diego Dermeval (diego.matos@famed.ufal.br)</li>
                <li className = " text-justify "> Alexandre Machado (alexandretopeca@gmail.com)</li>
                <li className = " text-justify "> Bruno Lemos (bruno.lemos.ec@gmail.com)</li>
                <li className = " text-justify "> Pedro Henrique (phbn@ic.ufal.br)</li>
                <li className = " text-justify "> Rodrigo Santos (rss3@ic.ufal.br)</li>
              </ul>
            </p>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
                <strong> Finalidade e Procedimento: </strong>A finalidade desta pesquisa de opinio  avaliar, no ponto de vista do professor, quais informaes dos estudantes melhor o auxilia  no processo de tomada de deciso durante o ensino/aprendizagem em uma plataforma de ensino gamificada. Com intuito de alcanar o objetivo da pesquisa, trs painis com diferentes dados dos estudantes sero analisados. O primeiro painel ter informaes apenas da interao dos estudantes com os recursos de aprendizagem do sistema. O segundo painel ter apenas informaes da interao dos estudantes com os elementos de gamificao do sistema. E por fim, o terceiro painel ter informaes relacionada tanto a interao dos alunos com os recursos de aprendizagem quanto com com os elementos de gamificao do sistema gamificado. Portanto, a sua participao nesta pesquisa consistir na realizao das seguintes atividades: <br/><br/>
                <ul>
                    <li className = " text-justify ">
                    Primeiro, voc ir responder um questionrio demogrfico, onde deve ser informado seu gnero, idade, nvel educacional que leciona e estado onde mora, assim como seu nvel de habilidade tcnica em relao ao uso de tecnologias de informao. 
                    </li>
                    <li className = " text-justify ">
                    Segundo, voc ir visualizar os 3 diferentes painis de dados em ordem aleatria. Aps a visualizao de cada um dos painis, voc ir responder a um questionrio com 9 perguntas, cujo o objetivo  avaliar sua compreenso, utilidade percebida e mudanas comportamentais em relao aos diferentes painis. 
                    </li>
                </ul>
            </p>
            <br/>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
                <strong> Potenciais Benefcios: </strong> Os resultados da pesquisa de opinio podem contribuir para um melhor entendimento de quais dados dos estudantes melhor apoia o processo de tomada de deciso de professores em um ambiente educacional gamificado.
            </p>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
                <strong> Potenciais Riscos: </strong> No h riscos conhecidos neste estudo.
            </p>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
                <strong> Confidencialidade: </strong> Ao participar deste estudo, voc reconhece e concorda que suas respostas e contribuies sero registradas anonimamente para fins de anlise de dados mais confiveis.
            </p>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
                <strong> Disseminao dos Resultados: </strong> Os resultados agregados deste estudo aparecero em artigos publicados em conferncias revisadas por pares e revistas cientficas nacionais e internacionais.
            </p>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
                <strong> Direito de Retirada: </strong> A participao neste estudo  voluntria, e voc pode decidir no participar a qualquer momento, ou optar por no responder a perguntas com as quais no se sinta confortvel. As respostas da pesquisa permanecero annimas. Uma vez que a pesquisa  annima, quando  submetida, no pode ser removida.
            </p>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
                <strong> Dvidas: </strong> Se voc tiver alguma dvida sobre o estudo, no hesite em perguntar a qualquer momento. Voc tambm est livre para entrar em contato com os pesquisadores se voc tiver dvidas no futuro. Este estudo no precisa ser submetido ao comit de tica, pois ele est avaliando apenas recursos para melhorar o software desenvolvido.
            </p>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
                <strong> Acompanhamento: </strong> Se voc tiver interesse em saber os resultados deste estudo, voc pode contatar os pesquisadores usando as informaes de contato fornecidas acima.
            </p>
            <p className = " text-justify " style={{fontFamily: 'Calibri', fontSize: '20px'}}>
                <strong> Consentimento para Participar: </strong> Ao preencher e submeter este questionrio, o seu consentimento livre e esclarecido  implcito e indica que voc entende as condies acima de participao neste estudo.
            </p>
            <br/>
            <Input type="checkbox" style={{position: 'relative', margin: '0 0.5em 0 0'}} onChange={this.handleCheckbox}/> 
            Declaro que compreendi os objetivos, riscos e benefcios de minha participao na pesquisa e que concordo em participar.<br/><br/>
                {!this.state.checkValid ? 
                    <center>
                    <Button disabled={!this.state.checkValid } color ="#C0B283" type="submit"> 
                        Continuar
                    </Button>
                    </center>
                  :
                    <Link className="text-dark" to={{pathname:"/demographicquest"}} onClick = {this.accept}>
                        <center>
                        <Button color ="#C0B283" type="submit"> 
                            Continuar
                            </Button>
                        </center>
                    </Link> }
            <br/>
            </Col>
            </div>
        )
    }
}

export default StarterTerm;
